<template>
<div>
    <el-button @click="show2 = !show2">Start</el-button>
    <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
    <p>場所指定してね{{date.region}}</p>
      <transition name="el-zoom-in-center" >
        <el-button v-show="show2"  class="transition-box" @click="add(0)" >京都</el-button>

      </transition>

      <transition name="el-zoom-in-top">
        <el-button v-show="show2" class="transition-box" @click="add(1)" >大阪</el-button>
      </transition>

      <transition name="el-zoom-in-bottom">
        <el-button  v-show="show2" class="transition-box" @click="add(2)">神戸</el-button >
      </transition>
    </div>

    <div class="block" v-show="show3" style="display: flex; margin-top: 50px; height: 100px;margin-left : 350px">
    <span class="demonstration">DateTime </span><br>
    <el-date-picker
      v-model="value4"
      type="datetimerange"
      range-separator="To"
      start-placeholder="Start date"
      end-placeholder="End date">
    </el-date-picker>
    
  </div>

  <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
      <transition name="el-zoom-in-center">
        <el-button v-show="show3" class="transition-box" @click="datetime">まったり〜</el-button>
      </transition>

      <transition name="el-zoom-in-top">
        <div v-show="show3" class="transition-box" @click="show4 = !show4">アクティブ（インドア）</div>
      </transition>

      <transition name="el-zoom-in-bottom">
        <div v-show="show3" class="transition-box">アクティブ（アウトドア）</div>
      </transition>
    </div>

    <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
      <transition name="el-zoom-in-center">
        <el-button v-show="show4" class="transition-box" @click="show5 = !show5">観光</el-button>
      </transition>

      <transition name="el-zoom-in-top">
        <div v-show="show4" class="transition-box" @click="show5 = !show5">ご飯andカフェ</div>
      </transition>

      <transition name="el-zoom-in-bottom">
        <div v-show="show4" class="transition-box">スポーツ</div>
      </transition>
    </div>

    <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
      <transition name="el-zoom-in-center">
        <el-button v-show="show5" class="transition-box" @click="show6 = !show6">3000円</el-button>
      </transition>

      <transition name="el-zoom-in-top">
        <div v-show="show5" class="transition-box" @click="show6 = !show6">5000円</div>
      </transition>

      <transition name="el-zoom-in-bottom">
        <div v-show="show5" class="transition-box">1万円</div>
      </transition>
    </div>

    <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
      <transition name="el-zoom-in-center">
        <el-button v-show="show6" class="transition-box" @click="show7 = !show7">車</el-button>
      </transition>

      <transition name="el-zoom-in-top">
        <div v-show="show6" class="transition-box" @click="show7 = !show7">徒歩</div>
      </transition>

      <transition name="el-zoom-in-bottom">
        <div v-show="show6" class="transition-box">電車</div>
      </transition>
    </div>

    <div style="display: flex; margin-top: 20px; height: 100px;margin-left : 350px">
    <el-button v-show="show7" class="transition-box" @click="post">送信</el-button>
    </div>
  </div>
</template>

<script>
    export default {
    data(){
      return{
        date:{
                region:null,
                date_start:null,
                date_end:null,
                tension:null,
                play:null,
                budget:null,
                move:null,
        },
        show2:false,
        show3:false,
        show4:false,
        show5:false,
        show6:false,
        show7:true,
        show8:true,
        value4: [new Date(2018, 10, 10, 10, 10), new Date(2018, 10, 11, 10, 10)]
        }
    },
    methods:{
        add(id){
            this.date.region = id
            console.log("aaaaaa")
            this.show3 =true
        },
        datetime(){
            this.date.date_start = parseInt(this.value4[0]/1000)
            this.date.date_end = parseInt(this.value4[1]/1000)
            console.log(this.date.date_start)
        },

        post(){
            // POST
            this.$http.post('http://localhost:9000', this.date, function (data, status, request) {
                console.log("post success")
                //status check
                console.log(status)
            }).error(function (data, status, request) {
                console.log("post failed")
            })
        }
    }
  }

</script>

<style>
  .transition-box {
    margin-bottom: 10px;
    width: 200px;
    height: 100px;
    border-radius: 4px;
    background-color: #409EFF;
    text-align: center;
    color: #fff;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-right: 20px;
  }
</style>



